import React, { Component } from 'react';
import DropDown from './dropDown';
import AddedNewPost from './AddedNewPost';

const style = {
  menuContainer: {
    border: '1px solid green',
    width: '300px',
  },
};

const initialState = [
  { id: 1, name: 'Max' },
  { id: 2, name: 'two' },
  { id: 3, name: 'three' },
];

class App extends Component {
  state = {
    inputText: null,
    task: initialState,
  };

  addTask = someText => {
    const info = {
      name: someText,
      id: Math.floor(Math.random() * 9999),
    };
    this.setState({ inputText: info });
    this.state.task.push(info);
  };

  Delete = id => {
    this.setState(prev => ({
      task: prev.task.filter(elem => elem.id !== +id),
    }));
  };

  render() {
    const { inputText, task } = this.state;
    return (
      <div style={style.menuContainer}>
        <p>Work</p>
        <AddedNewPost addTask={this.addTask} />
        <DropDown del={this.Delete} task={task} info={inputText} />
      </div>
    );
  }
}

export default App;
