import React, { Component } from 'react';
import PropTypes from 'prop-types';
import css from './AddedNewPost.module.css';

class AddedNewPost extends Component {
  constructor(props) {
    super(props);
    this.state = {
      info: '',
    };
  }

  handleSubmitFprm = evt => {
    evt.preventDefault();
    this.props.addTask(this.state.info);
  };

  handleGetInfo = evt => {
    this.setState({ info: evt.target.value });
  };

  render() {
    return (
      <form onSubmit={this.handleSubmitFprm}>
        <input
          className={css.getValue}
          onChange={this.handleGetInfo}
          type="text"
          placeholder="enter your task"
        />
      </form>
    );
  }
}

AddedNewPost.propTypes = {
  addTask: PropTypes.func.isRequired,
};

export default AddedNewPost;
